@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+Mono:wght@500&family=Open+Sans:ital,wght@1,300&display=swap");

* {
  margin: 0;
  padding: 0;
}
.locationContainer {
  width: 300px;
  height: 400px;
  position: relative;
  overflow: hidden;
  margin: 20px;
  display: inline-block;
}

.locationContainer:hover {
  box-shadow: 2px 2px 3px black;
  cursor: pointer;
}

.locationDesc {
  position: absolute;
  bottom: 0;
  background-color: rgba(15, 14, 14, 0.7);
  width: 100%;
  height: 15%;
  color: wheat;
  padding: 30px;
  transform: translateY(100%);
  transition: all 0.3s ease-in-out;
  font-family: "Noto Sans Mono", monospace;
}

.show {
  transform: translateY(0);
}
.image {
  position: absolute;
  height: 100%;
  width: 100%;
}
